<template>
  <div class="skkm">
    <div class="container-fluid p-0">
      <div class="row">
        <div class="col-md-12 mb-5">
          <h1 class="h1 mb-0">Sistem Kredit Kegiatan Mahasiswa (SKKM)</h1>
        </div>
        <div class="col-md-12 mb-5">
          <div class="row" style="padding: 0; margin: 0">
            <div class="col-sm-3">
              <div class="row">
                <div class="col-xs">
                  <img
                    src="../../assets/img/icon/file-text.svg"
                    alt=""
                    height="50"
                  />
                </div>
                <div class="col-xs">
                  <h1 class="font-weight-bold">
                    {{ getDataSkkm.skkm_fillable }}
                  </h1>
                  <small>Total Skkm yang telah ditempuh </small>
                </div>
              </div>
            </div>
            <div class="col-sm-3">
              <div class="row">
                <div class="col-xs">
                  <img
                    src="../../assets/img/icon/file.svg"
                    alt=""
                    height="50"
                  />
                </div>
                <div class="col-xs">
                  <h1 class="font-weight-bold">
                    {{ getDataSkkm.skkm_required - getDataSkkm.skkm_fillable }}
                  </h1>
                  <small>Sisa Skkm yang harus ditempuh</small>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-md-12">
          <div class="card">
            <div class="card-body">
              <div id="skkmtable">
                <div class="table-striped table-responsive">
                  <h5 class="card-title">Data SKKM</h5>
                  <table class="table mb-0">
                    <thead>
                      <tr>
                        <th style="width: 5%" scope="col">No</th>
                        <th style="width: 10%" scope="col">Bidang</th>
                        <th style="width: 30%" scope="col">Kegiatan</th>
                        <th scope="col">Tingkat</th>
                        <th scope="col">Partisipasi</th>
                        <th scope="col">Bobot Skkm</th>
                        <th scope="col">Detail</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(allskkm, index) in getDataSkkm.skkm_list"
                        :key="number(index)"
                      >
                        <td class="text-center">{{ number(index) }}</td>
                        <td>
                          {{ allskkm.event_category }}
                        </td>
                        <td>{{ allskkm.event_name }}</td>
                        <td>{{ allskkm.level }}</td>
                        <td class="text-center">{{ allskkm.participant }}</td>
                        <td class="text-center">{{ allskkm.skkm_credits }}</td>
                        <td>
                          <button
                            class="btn-success btn"
                            data-toggle="modal"
                            :data-target="'#detail' + number(index)"
                            @click.prevent="getDetailSkkm(allskkm.skkm_id)"
                          >
                            Lihat
                          </button>
                          <!-- The Modal -->
                          <div
                            class="modal text-left"
                            :id="'detail' + number(index)"
                          >
                            <div class="modal-dialog">
                              <div class="modal-content">
                                <!-- Modal Header -->
                                <div class="modal-header">
                                  <h4 class="modal-title">
                                    <svg
                                      width="24"
                                      height="24"
                                      viewBox="0 0 49 49"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        d="M24.5 49C38.031 49 49 38.031 49 24.5C49 10.969 38.031 0 24.5 0C10.969 0 0 10.969 0 24.5C0 38.031 10.969 49 24.5 49Z"
                                        fill="#D7D0F6"
                                      />
                                      <path
                                        d="M24 36V24"
                                        stroke="white"
                                        stroke-width="6"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                      />
                                      <path
                                        d="M24 12H24.0292"
                                        stroke="white"
                                        stroke-width="6"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                      />
                                    </svg>
                                    Detail Data SKKM
                                  </h4>
                                  <button
                                    type="button"
                                    class="close"
                                    data-dismiss="modal"
                                  >
                                    &times;
                                  </button>
                                </div>

                                <!-- Modal body -->
                                <div class="modal-body">
                                  <div class="form-group">
                                    <label for="bidang">Bidang</label>
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="allskkm.event_category"
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="kegiatan">Kegiatan</label>
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="allskkm.event_name"
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="ketua"
                                      >Ketua Penyelenggara</label
                                    >
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="getDataSkkmDetail.author"
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="tanggal"
                                      >Tanggal Kegiatan</label
                                    >
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="getDataSkkmDetail.date_start"
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="tingkat">Tingkat</label>
                                    <input
                                      type="text"
                                      class="form-control"
                                      :value="getDataSkkmDetail.level"
                                      placeholder=""
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="partisipasi">Partisipasi</label>
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="getDataSkkmDetail.participant"
                                      disabled
                                    />
                                  </div>
                                  <div class="form-group">
                                    <label for="skkm">SKKM</label>
                                    <input
                                      type="text"
                                      class="form-control"
                                      placeholder=""
                                      :value="getDataSkkmDetail.skkm_credits"
                                      disabled
                                    />
                                  </div>
                                </div>

                                <!-- Modal footer -->
                                <div class="modal-footer">
                                  <button
                                    type="button"
                                    class="btn btn-outline-secondary"
                                    data-dismiss="modal"
                                  >
                                    Tutup
                                  </button>
                                </div>
                              </div>
                            </div>
                          </div>
                          <!-- batas modal -->
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "skkm",
  data() {
    return {
      allskkm: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataSkkm: "infoskkm/skkm",
      getDataSkkmDetail: "infoskkmdetail/skkm_detail",
    }),
  },
  methods: {
    ...mapActions({
      infoskkm: "infoskkm/skkm",
      infoskkmdetail: "infoskkmdetail/skkm_detail",
    }),
    number: function (index) {
      return index + 1;
    },
    getSKKMall() {
      this.infoskkm();
    },
    async getDetailSkkm(skkm_id) {
      this.infoskkmdetail(skkm_id);
    },
  },

  mounted() {
    this.getSKKMall();
    $("title").html("Sistem Kredit Kegiatan Mahasiswa| STTBANDUNG");
  },
};
</script>

<style>
</style>